import React from 'react';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Card from 'react-bootstrap/Card';
import Table from 'react-bootstrap/Table';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Modal from 'react-bootstrap/Modal';
import Highcharts from 'highcharts';
import $ from  'jquery';
import HeaderMenu from "../../components/HeaderMenu";

class PatientSelf extends React.Component{
	componentDidMount(){
		// 图表配置
        var options = {
            chart: {
                type: 'line'                          //指定图表的类型，默认是折线图（line）
            },
			title: {
                text: '0: Bad | 10: Good'                 // 标题
            },
            xAxis: {
                categories: ['2019/11/20', '2019/11/23', '2019/11/26','2019/12/10','2019/12/14','2019/12/20']   // x 轴分类
            },
            yAxis: {
                title: {
                    text: 'Total Score'                // y 轴标题
                }
            },
            series: [{                              // 数据列
                name: 'distance walked',                        // 数据列名
                data: [0, 3, 3, 4, 6, 9]                     // 数据
            }, {
                name: 'pain level',
                data: [10, 10, 9, 7, 4, 2]
            }, {
                name: 'joint comfortness',
                data: [1, 2, 2,5,8,9.5]
            }]
        };
        // 图表初始化函数
		var chart = Highcharts.chart('container', options);
		$(".model").hide();
		$(".sshuju").hide();
		$(".sform").hide();
		$(".model").hide();
		$(".sjindu").show();

		$(".jindu").click(function(){
			$(".sshuju").hide();
			$(".sjindu").show();
			$(".sform").hide();
		});

		$(".form").click(function(){
			$(".sjindu").hide();
			$(".sshuju").hide();
			$(".sform").show();
		});

		$(".shuju").click(function(){
			$(".sshuju").show();
			$(".sjindu").hide();
			$(".sform").hide();
		});

		$(".submit_btn").click(function(){
			$(".model").fadeIn(100);
		});

		$(".close_btn").click(function(){
			$(".model").hide();
		});

		$(".close").click(function(){
			$(".model").hide();
		});
	}

	render(){
		return(
			<div>
				<HeaderMenu />
				<Container>
				<div style={{height: '50px'}}></div>
				<div style={{display:'flex'}}>
					<div style={{width:'50%'}}>
						<p>Patient Number: 21406</p>
						<p>Name: Kim</p>
						<p>Age: 65</p>
						<p>Sex: M</p>
						<p>Type: Fracture</p>
						<p>Your GP: Doctor.Jack</p>
					</div>
					<div style={{width:'40%', alignSelf:'center', textAlign:'center'}}>
						<img src="https://upload.wikimedia.org/wikipedia/commons/5/5f/User_with_smile.svg" style={{width:'40%'}} alt=""></img>
					</div>
				</div>
				<Card>
				<Card.Header>Details</Card.Header>
				<Card.Body>
					<Card.Title></Card.Title>
					<a href="javascript:;" className="btn btn-primary btn-lg jindu">My Progress</a>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
					<a href="javascript:;" className="btn btn-primary btn-lg shuju">My Data </a>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
					<a href="javascript:;" className="btn btn-primary btn-lg form">My Survey</a>
					<Card.Text>
					<div style={{height: '40px'}}></div>
					<div style={{display:'flex'}} className="sjindu">
						<div style={{width:'100%'}}>
							<Table striped bordered hover>
							<thead>
								<tr>
								<th colSpan="2">Doctor's log</th>
								<th colSpan="2">Date</th>
								<th colSpan="4">Record</th>
								<th colSpan="2">Severity</th>
								</tr>
							</thead>
							<tbody>
								<tr>
								<td>Outpatient</td>
								<td colSpan="3">2019-11-20</td>
								<td colSpan="3">Calf fracture</td>
								<td colSpan="3">Serious</td>
								</tr>
								<tr>
								<td>Osteosynthesis</td>
								<td colSpan="3">2019-11-23</td>
								<td colSpan="3">Already boned but fragile</td>
								<td colSpan="3">Successful operation</td>
								</tr>
								<tr>
								<td>Reinforced Support</td>
								<td colSpan="3">2019-11-23</td>
								<td colSpan="3">Bone into 20cm bracket</td>
								<td colSpan="3">Successful Access</td>
								</tr>
								<tr>
								<td>Be hospitalized</td>
								<td colSpan="3">2019-11-23</td>
								<td colSpan="3">Single VIP sickroom: No.2</td>
								<td colSpan="3">No other side effects</td>
								</tr>
								<tr>
								<td>Physiotherapy</td>
								<td colSpan="3">2019-11-26</td>
								<td colSpan="3">one hour of rehabilitation training</td>
								<td colSpan="3">Calf regained consciouseness</td>
								</tr>
								<tr>
								<td>Physiotherapy</td>
								<td colSpan="3">To 2019-12-10</td>
								<td colSpan="3">End of recovery traning</td>
								<td colSpan="3">Can walking</td>
								</tr>
								<tr>
								<td>Bracket Surgey</td>
								<td colSpan="3">2019-12-14</td>
								<td colSpan="3">Remove the bracket</td>
								<td colSpan="3">Get Well</td>
								</tr>
							</tbody>
							</Table>
						</div>
					</div>
					<div id="container" style={{width: '900px', height: '500px'}} className="sshuju"></div>
					<div className="sform">
					<Form>
					<Form.Group as={Row} controlId="formHorizontalEmail">
						<Form.Label column sm={1}>
						Date
						</Form.Label>
						<Col sm={10}>
						<Form.Control type="datetime-local" />
						</Col>
					</Form.Group>

					<Form.Group as={Row} controlId="formHorizontalPassword" style={{marginLeft: '0px', marginRight: '2px'}}>
					{['checkbox'].map(type => (
						<div key={`inline-${type}`} className="mb-3">
							<Form.Label column sm={100}>
						Position
						</Form.Label>
						<Form.Check inline label="Knee" type={type} id={`inline-${type}-1`} />
						<Form.Check inline label="Thigh" type={type} id={`inline-${type}-2`} />
						<Form.Check inline label="Calf" type={type} id={`inline-${type}-3`} />
						<Form.Check inline label="Foot ring" type={type} id={`inline-${type}-4`} />
						</div>
					))}
					</Form.Group>
					<p>Distance walked</p>
					<Form.Group as={Row} controlId="formHorizontalPassword" style={{marginLeft: '0px', marginRight: '2px'}}>
					{['checkbox'].map(type => (
						<div key={`inline-${type}`} className="mb-3">
						<Form.Check inline label="Very Good" type={type} id={`inline-${type}-1`} />
						<Form.Check inline label="Good" type={type} id={`inline-${type}-2`} />
						<Form.Check inline label="Bad" type={type} id={`inline-${type}-3`} />
						<Form.Check inline label="Very Bad" type={type} id={`inline-${type}-4`} />
						</div>
					))}
					</Form.Group>
					<p>Muscle ache</p>
					<Form.Group as={Row} controlId="formHorizontalPassword" style={{marginLeft: '0px', marginRight: '2px'}}>
					{['checkbox'].map(type => (
						<div key={`inline-${type}`} className="mb-3">
						<Form.Check inline label="Very Good" type={type} id={`inline-${type}-1`} />
						<Form.Check inline label="Good" type={type} id={`inline-${type}-2`} />
						<Form.Check inline label="Bad" type={type} id={`inline-${type}-3`} />
						<Form.Check inline label="Very Bad" type={type} id={`inline-${type}-4`} />
						</div>
					))}
					</Form.Group>
					<p>Pain level</p>
					<Form.Group as={Row} controlId="formHorizontalPassword" style={{marginLeft: '0px', marginRight: '2px'}}>
					{['checkbox'].map(type => (
						<div key={`inline-${type}`} className="mb-3">
						<Form.Check inline label="Very Good" type={type} id={`inline-${type}-1`} />
						<Form.Check inline label="Good" type={type} id={`inline-${type}-2`} />
						<Form.Check inline label="Bad" type={type} id={`inline-${type}-3`} />
						<Form.Check inline label="Very Bad" type={type} id={`inline-${type}-4`} />
						</div>
					))}

					</Form.Group>
					<p>Comfortness</p>
					<Form.Group as={Row} controlId="formHorizontalPassword" style={{marginLeft: '0px', marginRight: '2px'}}>
					{['checkbox'].map(type => (
						<div key={`inline-${type}`} className="mb-3">
						<Form.Check inline label="Very Good" type={type} id={`inline-${type}-1`} />
						<Form.Check inline label="Good" type={type} id={`inline-${type}-2`} />
						<Form.Check inline label="Bad" type={type} id={`inline-${type}-3`} />
						<Form.Check inline label="Very Bad" type={type} id={`inline-${type}-4`} />
						</div>
					))}

					</Form.Group>
					<p>Degree of swelling</p>
					<Form.Group as={Row} controlId="formHorizontalPassword" style={{marginLeft: '0px', marginRight: '2px'}}>
					{['checkbox'].map(type => (
						<div key={`inline-${type}`} className="mb-3">
						<Form.Check inline label="Very Good" type={type} id={`inline-${type}-1`} />
						<Form.Check inline label="Good" type={type} id={`inline-${type}-2`} />
						<Form.Check inline label="Bad" type={type} id={`inline-${type}-3`} />
						<Form.Check inline label="Very Bad" type={type} id={`inline-${type}-4`} />
						</div>
					))}

					</Form.Group>
					<p>Perception</p>
					<Form.Group as={Row} controlId="formHorizontalPassword" style={{marginLeft: '0px', marginRight: '2px'}}>
					{['checkbox'].map(type => (
						<div key={`inline-${type}`} className="mb-3">
						<Form.Check inline label="Very Good" type={type} id={`inline-${type}-1`} />
						<Form.Check inline label="Good" type={type} id={`inline-${type}-2`} />
						<Form.Check inline label="Bad" type={type} id={`inline-${type}-3`} />
						<Form.Check inline label="Very Bad" type={type} id={`inline-${type}-4`} />
						</div>
					))}


					</Form.Group>
					<Button variant="primary" className="submit_btn">
						Submit
					</Button>
					</Form>
					</div>
					</Card.Text>
				</Card.Body>
				</Card>
				<div className="model">
				<div className="modal-dialog" style={{position: 'absolute', top: '0', left: '0', marginLeft: '39%'}}>
					<div className="modal-content">
					<Modal.Header closeButton>
						<Modal.Title>Thank for submitting</Modal.Title>
					</Modal.Header>

					<Modal.Body>
						<p>We will give you feedback and upadate the survey to your data.</p>
					</Modal.Body>

					<Modal.Footer>
						<Button variant="secondary" className="close_btn" block>Close</Button>
					</Modal.Footer>
					</div>
				</div>
				</div>
				</Container>

			</div>
		);
	}
}

export default PatientSelf;
