import React from 'react';

import Container from 'react-bootstrap/Container';
import Table from 'react-bootstrap/Table';
import $ from  'jquery';

import HeaderMenu from "../../components/HeaderMenu";

class PatientRecords extends React.Component{
	componentDidMount(){
        $.getUrlParam = function(name) {
            var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)"); //构造一个含有目标参数的正则表达式对象
            var r = window.location.search.substr(1).match(reg);  //匹配目标参数
            if (r != null) return unescape(r[2]); return null; //返回参数值
        };
        var patientno = $.getUrlParam('patientno');
        var name = $.getUrlParam('name');
		var birthday = $.getUrlParam('birthday');
		$(".sdetail").hide();
		$(".sreport").hide();
		$(".sinfo").show();
		$(".patientno").html("PatientRecords No " + patientno);
		$(".name").html("Name " + name);
		$(".birthday").html("Date Of Surgery " + birthday);
		$(".info").click(function(){
			$(".sreport").hide();
			$(".sinfo").show();
			$(".sdetail").hide();
		});

		$(".detail").click(function(){
			$(".sreport").hide();
			$(".sinfo").hide();
			$(".sdetail").show();
		});

		$(".report").click(function(){
			$(".sreport").show();
			$(".sinfo").hide();
			$(".sdetail").hide();
		});


    }
	render(){
		return(
			<div>
                <HeaderMenu />
				<Container>
					<div style={{height: '50px'}}></div>
						<div style={{width:'30%'}}>
							<p style={{marginBottom: '40px'}}><a href="javascript:;" className="btn btn-primary btn-lg info">Patient's Information</a></p>
							<p style={{marginBottom: '40px'}}><a href="javascript:;" className="btn btn-primary btn-lg detail">Surgery Details</a></p>
							<p><a href="javascript:;" className="btn btn-primary btn-lg report">Report</a></p>
						</div>
						<div style={{width:'50%', display:'flex'}} className="sinfo">
							<div style={{width:'50%'}}>
								<p className="name">Patient No 21406</p>
								<p className="patientno">Patient No 21406</p>
								<p>Age 65</p>
								<p>Sex M</p>
								<p>Patient Type Important</p>
								<p className="birthday">Data Of Surgery 31/10/2019</p>
							</div>
							<img src="https://upload.wikimedia.org/wikipedia/commons/5/5f/User_with_smile.svg" style={{width:'35%', height:'75%'}} alt=""></img>
						</div>
						<div style={{width:'50%', display:'flex'}} className="sdetail">
							<div style={{width:'100%'}}>
								<Table striped bordered hover>
								<thead>
									<tr>
									<th colSpan="4">Surgery Details</th>
									</tr>
								</thead>
								<tbody>
									<tr>
									<td>Knee Surgery</td>
									<td colSpan="2">24-12-2019</td>
									</tr>
									<tr>
									<td>Scrubbing</td>
									<td colSpan="2">1-1-2020</td>
									</tr>
									<tr>
									<td>Physiotherapy</td>
									<td colSpan="2">2-2-2020</td>
									</tr>
								</tbody>
								</Table>
								{/* <div id="container" style={{width: '900px', height: '500px'}} className="sshuju"></div> */}
							</div>
						</div>
						<div style={{width:'50%', display:'flex'}} className="sreport">
							<div style={{width:'50%'}}>
								<p>Report</p>
								<p>x x x x x x x x x x x x x x x x</p>
								<p>x x x x x x x x x x x x x x x x</p>
								<p>x x x x x x x x x x x x x x x x</p>
								<p>x x x x x x x x x x x x x x x x</p>
								<p>x x x x x x x x x x x x x x x x</p>
								<p>x x x x x x x x x x x x x x x x</p>
							</div>
						</div>
				</Container>
			</div>
		);
	}
}

export default PatientRecords;

