import React from 'react';
import Form from 'react-bootstrap/Form';
import $ from  'jquery';
import HeaderMenu from "../components/HeaderMenu";

class Register extends React.Component{
    handleClick() {
        alert('Reset password e-mail has been sent.')
    }
    componentDidMount(){
        $.getUrlParam = function(name) {
            var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)"); //构造一个含有目标参数的正则表达式对象
            var r = window.location.search.substr(1).match(reg);  //匹配目标参数
            if (r != null) return unescape(r[2]); return null; //返回参数值
        }

        $(".login").click(function(){
            window.location.href = "/Patient?id=1";
        })
    }
	render(){
		return(
			<div>
				<HeaderMenu />
				<div style={{display: 'flex', alignSelf:'center', justifyContent: 'center', marginTop: '10%'}}>
                    <Form>
                    <Form.Group controlId="formBasicEmail">
                        <Form.Label>Email address</Form.Label>
                        <Form.Control type="email" placeholder="Enter email" />
                        <Form.Text className="text-muted">
                        We'll never share your email with anyone else.
                        </Form.Text>
                    </Form.Group>

                    <Form.Group controlId="formBasicPassword">
                        <Form.Label>Password</Form.Label>
                        <Form.Control type="password" placeholder="Password" />
                    </Form.Group>

                    <Form.Group controlId="formBasicPassword">
                        <Form.Label>Confirmation Password</Form.Label>
                        <Form.Control type="password" placeholder="Password" />
                    </Form.Group>
                    <Form.Group controlId="formBasicCheckbox">
                        <Form.Check type="checkbox" label="Check me out" />
                    </Form.Group>
                    <a href="javascript:;" className="btn btn-primary btn-block login">Submit</a>
                    </Form>
                </div>
			</div>

		);
	}
}

export default Register;
